main.empire-home-section {
    padding-top: 18vh;
    color: var(--baseWhite);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    font-family: 'Poppins';
    background-color: var(--baseBlue);
}

main.empire-home-section h1 {
    font-family: 'Poppins';
    font-weight: 700;
}

main.empire-home-section h1 img {
    margin: 0 2vw;
}

main.empire-home-section p {
    width: 50%;
    font-weight: 300;
    color: var(--shade1Gray);
}

/* title card */
main.sliding-content div.empire-title-card {
    height: calc(5vh + 20vw);
    width: 15vw;
    margin: 0 2vw 0 0;
    padding: 1vw;
    border-radius: 0.4vw;
    background: var(--shade4Blue);
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 0.8vw;
}

main.sliding-content div.empire-title-card h2 {
    font-size: 1.8vw;
    color: var(--shade8Blue);
}

main.sliding-content div.empire-title-card p {
    width: 90%;
    color: var(--shade1Gray);
}

/* slider css */
main.empire-home-section main.sliding-content {
    display: flex;
    align-items: center;
    margin-top: 3vw;
    width: 90vw;
}

main.sliding-content div.empire-slider {
    overflow-x: auto;
    overflow-y: hidden;
    height: calc(5vh + 20vw);
    max-width: 80vw;
    white-space: nowrap;
}

div.empire-slider::-webkit-scrollbar {
    width: 6px;
    height: 6px;
}

div.empire-slider:hover::-webkit-scrollbar {
    height: 10px;
}

div.empire-slider::-webkit-scrollbar-track {
    border: 0;
    background: var(--dullWhite);
    border-radius: 10px;
    margin-left: 10vh;
    margin-right: 10vh;
}

div.empire-slider::-webkit-scrollbar-track:hover {
    background: var(--baseBlue);
}

div.empire-slider::-webkit-scrollbar-thumb {
    background: var(--baseOrange);
    border-radius: 10px;
}

div.empire-slider::-webkit-scrollbar-thumb:hover {
    background: #d19c7b;
}

div.empire-slider div.founders-side-plane-wrapper {
    display: inline-block;
    position: absolute;
    margin-left: 4vw;
    margin-top: 4vw;
}

img.founders-side-plane {
    max-width: 20vw;
    margin-bottom: auto;
}

div.shadow-wrapper {
    background-image: linear-gradient(to right,
            rgba(255, 255, 255, 0),
            var(--baseBlue));
    height: calc(5vh + 20vw);
    width: 7vw;
    right: 3.7vw;
    position: absolute;
    z-index: 3;
}

/* tab */
div#dream-team-content,
div#community-content,
div#dreamersclub-content,
div#dreamerclub-content,
div#fam-content {
    margin-top: 3vw;
    margin-bottom: 3vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* nav borders */
main.empire-home-section .nav-tabs {
    border: none;
}

main.empire-home-section .nav-tabs::after {
    content: '';
    height: 1px;
    width: 100%;
    background-color: var(--baseWhite);
    position: relative;
    display: block;
}

div#nav-shadow-wrapper {
    height: 0;
}

div#nav-shadow-div {
    position: absolute;
    height: 3vw;
    width: 4vw;
    right: calc((100vw - (15vw * 3 + 20vw)) / 2);
    background-image: linear-gradient(to right,
            rgba(255, 255, 255, 0),
            var(--baseBlue));
    z-index: 2;
}

/* dream team */
div#dream-team-content div.card.empire-profile-card div.dream-team-profile {
    display: flex;
    flex-direction: row;
    border-radius: 0 0 1vw 1vw;
    background: var(--shade4Blue);
    align-items: center;
    justify-content: space-between;
}

/* profile card */
div.card.empire-profile-card {
    margin: 0 2vh 2vh 2vh;
    background: none;
    border: none;
    display: inline-block;
}

div.card.empire-profile-card:hover {
    transform: scale(1.01);
}

main.sliding-content div.empire-slider div.card.empire-profile-card img.potrait {
    height: calc(2vh + 15vw);
}

div.card.empire-profile-card div.profile-intro-text {
    display: flex;
    flex-direction: column;
    padding: 1vw;
    border-radius: 0 0 1vw 1vw;
    background: var(--shade4Blue);
    text-align: left;
}

div.card.empire-profile-card div.profile-intro-text div.name {
    font-size: calc(0.5em + 0.5vw);
    font-weight: 700;
}

div.card.empire-profile-card div.profile-intro-text div.desc {
    font-size: calc(0.5em + 0.3vw);
    white-space: pre-wrap;
}

div.profile-card div.profile {
    display: flex;
    flex-direction: row;
    border-radius: 0 0 1vw 1vw;
    background: var(--shade4Blue);
    align-items: center;
    justify-content: space-between;
}

div.profile-card div.profile div.work-logo {
    width: 3vw;
    height: 3vw;
    margin-right: 1vw;
    border-radius: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* mentors profile card */
div#community-content div.profile-card div.identity-wrapper {
    height: 0;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

div#community-content div.profile-card div.identity-wrapper main.identity-content {
    color: var(--baseWhite);
    min-width: 10vw;
    font-size: 1vw;
    background: var(--shade4Blue);
    padding: 2px 5px;
    border-radius: 4px 4px 0 0;
}

div.work-logo img.work-logo {
    width: 3vw;
    height: 3vw;
}

div#root div#community-content div.profile-card div.profile div.work-logo[data-small-logo='true'],
div#root div#community-content div.profile-card div.profile div.work-logo[data-small-logo='true'] img {
    width: 2.5vw;
    height: 2.5vw;
    margin-top: auto;
    margin-bottom: 0.4vw;
}

/* dreamerclub+explorersclub */
main.dreamerclub-flexbox,
main.fam-flexbox {
    width: 90vw;
    display: flex;
    flex-wrap: wrap;
    position: relative;
}

div.horizontal-plane-wrapper {
    height: 18vw;
    width: 18vw;
    position: absolute;
    bottom: 0;
}

div.horizontal-plane-wrapper.Xflipped {
    right: 0;
}

img.side-plane {
    max-width: 100%;
}

img.Xflipped {
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}

p.dreamerclub-flexbox,
p.fam-flexbox {
    margin-left: auto;
    margin-right: auto;
}

main.dreamerclub-flexbox div.empire-card-box,
main.fam-flexbox div.empire-card-box {
    width: 30vw;
    padding: 1vw;
    margin-left: auto;
    margin-right: auto;
}

main.dreamerclub-flexbox div.empire-card-box div.empire-profile-card {
    width: 20vw;
}

main.dreamerclub-flexbox div.empire-card-box div.empire-profile-card img {
    max-width: 20vw;
}

main.fam-flexbox div.empire-card-box div.card.empire-profile-card img {
    max-width: 20vw;
}

main.dreamerclub-flexbox a.passion-link {
    text-decoration: none;
    font-size: calc(0.5em + 0.5vw);
    color: var(--shade3Orange);
    width: 50%;
    white-space: nowrap;
}

.BlueBg {
    background-color: var(--lightBlue);
    min-width: 18vw;
    overflow: hidden;
    border-radius: 1vw 1vw 0 0;
}

main.fam-flexbox .BlueBg,
main.dreamerclub-flexbox .BlueBg {
    width: auto;
}

.linkedin:hover a{
    color: var(--baseOrange)!important;
}

